*{
margin: 0;
padding: 0;
}

.app-input
{
    text-align: center;
    color: rgb(89, 89, 223);
}

h1{
    color: green;
}

.app-input input{
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid rgb(26, 30, 235);
    outline: none;
    padding: 10px 5px;
    width: 200px;
}

.app-input button{
    background-color: rgb(113, 9, 211);
    color: white;
    border: none;
    outline: none;
    margin-top: 10px;
    padding: 10px 30px;
    font-size: 16px;
    cursor: pointer;
}

nav {
    background-color: violet;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 5px;


}

nav ul{
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    color: white;
    font-weight: bold;
}

nav ul li{
    cursor: pointer;
    font-size: 14px;
    transition: all 0.5s ease-in-out;
}

nav ul li:hover{
    transform: scale(1.4);
}
.navActive{
    color: rgb(89, 170, 8);
    font-size: 30px;
    font-weight: bold;
}

.app-container{
    background-color: rgb(0, 225, 255);
    font-size: 30px;
}

